/** ----- colors ----- */
.b-add-schedule-card {
  border-bottom: 1px solid #0b4e35;
  margin-bottom: 30px;
  padding: 20px 15px 40px;
  position: relative; }
  .b-add-schedule-card__field--left, .b-add-schedule-card__field--right {
    display: inline-block;
    width: 50%; }
  .b-add-schedule-card__field--left {
    padding-right: 15px; }
  .b-add-schedule-card__field--right {
    padding-left: 15px; }
  .b-add-schedule-card__title-section {
    font-family: "montserratbold";
    font-size: 16px;
    line-height: 1.4;
    margin-bottom: 5px;
    margin-bottom: 15px; }
  .b-add-schedule-card__check-list {
    margin: 0;
    padding-left: 0; }
  .b-add-schedule-card__check-item {
    list-style-type: none; }
    .b-add-schedule-card__check-item:not(:last-child) {
      margin-bottom: 24px; }
    .b-add-schedule-card__check-item .b-checkbox {
      margin-bottom: 0; }
  .b-add-schedule-card__delete {
    width: 25px;
    position: absolute;
    right: 5px;
    top: -10px;
    padding: 3px;
    cursor: pointer; }

@media (min-width: 768px) {
  .b-add-schedule-card {
    padding: 30px;
    margin: 0px auto 30px;
    max-width: 520px;
    overflow: hidden;
    border-radius: 5px;
    border: 1px solid #ebebeb;
    background-color: #fff; }
    .b-add-schedule-card__delete {
      right: 0px;
      top: 0px;
      width: 38px;
      padding: 9px;
      border-left: 1px solid transparent;
      border-bottom: 1px solid transparent; }
      .no-touchevents .b-add-schedule-card__delete:hover,
      .touchevents .b-add-schedule-card__delete:active {
        border-color: rgba(0, 0, 0, 0.1); } }



