/** ----- colors ----- */
.b-trainer-info {
  padding: 40px 15px 0px; }
  .b-trainer-info__playground-list {
    padding-left: 0px;
    margin-top: 0px;
    margin-bottom: 30px; }
  .b-trainer-info__playground-item {
    display: inline-block;
    list-style-type: none;
    width: 100%; }
    .b-trainer-info__playground-item:not(:last-child) {
      margin-bottom: 15px; }
  .b-trainer-info__title-field {
    font-family: "montserratbold";
    font-size: 16px;
    line-height: 1.4;
    margin-bottom: 5px; }
    .b-trainer-info__title-field--light {
      font-family: "montserratmedium"; }
  .b-trainer-info__cost {
    font-size: 0; }
  .b-trainer-info__button {
    margin-bottom: 30px; }

@media (min-width: 768px) {
  .b-trainer-info {
    padding: 50px 15px 0px;
    background-color: #e3e5e8; }
    .b-trainer-info__form {
      max-width: 480px;
      margin: 0px auto;
      background-color: #fff;
      padding: 30px;
      overflow: hidden;
      border-radius: 5px;
      border: 1px solid #ebebeb;
      margin-bottom: 30px; }
    .b-trainer-info__playground {
      max-width: 480px;
      min-height: 230px;
      margin: 0px auto;
      padding: 30px;
      border-radius: 5px;
      background-color: #fff;
      margin-bottom: 50px;
      border-top: 1px solid;
      border-bottom: 1px solid;
      border-color: #ebebeb; }
    .b-trainer-info__playground-list {
      margin-left: -7px;
      margin-right: -7px;
      margin-bottom: 0; }
    .b-trainer-info__playground-item {
      padding-left: 7px;
      padding-right: 7px;
      width: 50%; }
    .b-trainer-info__button {
      text-align: center;
      padding-bottom: 50px; } }

@media (min-width: 1024px) {
  .b-trainer-info__form {
    max-width: 520px; }
  .b-trainer-info__playground {
    max-width: 520px; }
  .b-trainer-info__playground-item {
    width: calc(100% / 2); } }



