/** ----- colors ----- */
.b-trainer-add-schedule {
  background-color: #0b4e35; }
  .b-trainer-add-schedule__calendar {
    border-top: 1px solid #06281b;
    z-index: 20; }
  .b-trainer-add-schedule__add-more {
    text-align: center;
    padding: 5px 0px 30px; }
  .b-trainer-add-schedule__save {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    padding: 0 16px 16px;
    background-color: #fff; }
  .b-trainer-add-schedule__schedule {
    border-radius: 15px 15px 0 0;
    background-color: #fff;
    padding: 15px 15px 100px; }

@media (min-width: 768px) {
  .b-trainer-add-schedule__schedule {
    background-color: #e3e5e8; }
  .b-trainer-add-schedule__save {
    background-color: #e3e5e8; } }

@media (min-width: 1024px) {
  .b-trainer-add-schedule__save {
    width: calc(100% - 85px);
    left: auto;
    right: 0px; }
  .b-trainer-add-schedule__schedule {
    border-radius: 0; } }

@media (min-width: 1280px) {
  .b-trainer-add-schedule {
    display: flex;
    min-height: 100%; }
    .b-trainer-add-schedule__calendar {
      margin: 0;
      order: 1;
      flex-shrink: 0;
      padding-top: 78px;
      box-shadow: -2px 0px 3px -2px #c5c5c5; }
      .b-trainer-add-schedule__calendar .b-date-calendar {
        position: sticky;
        top: 0; }
    .b-trainer-add-schedule__schedule {
      width: 100%;
      padding-top: 20px; }
    .b-trainer-add-schedule__save {
      width: calc(100% - 405px);
      right: auto;
      left: 50%;
      transform: translateX(-50%);
      margin-left: -117px;
      max-width: 1035px; } }



