/** ----- colors ----- */
.b-schedule-item {
  cursor: pointer;
  position: relative;
  padding: 15px;
  margin: 0 -15px;
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd; }
  .b-schedule-item__time-wrap {
    display: flex;
    align-items: center; }
  .b-schedule-item__time {
    color: #222222;
    font-family: "montserratmedium";
    font-size: 18px;
    line-height: 1.4; }
    .b-schedule-item__time--finish {
      color: #cccccc; }
  .b-schedule-item__info {
    width: 100%; }
  .b-schedule-item__state {
    font-family: "montserratregular";
    font-size: 18px;
    line-height: 26px;
    position: relative;
    padding: 0 15px; }
    .b-schedule-item__state:before {
      content: '';
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      position: absolute;
      top: 10px;
      left: 0px;
      background-color: transparent; }
    .b-schedule-item__state--free::before {
      background-color: #29cb41; }
    .b-schedule-item__state--busy::before {
      background-color: #ff6158; }
  .b-schedule-item__state-name {
    vertical-align: middle; }
  .b-schedule-item__court-info {
    font-family: "montserratregular";
    font-size: 14px;
    line-height: 18px;
    color: #8c8c8c; }
  .b-schedule-item__click {
    font-family: "montserratregular";
    font-size: 14px;
    line-height: 1.2;
    margin-top: 10px;
    color: #0b4e35;
    text-align: right; }
  .b-schedule-item__name-court {
    font-family: "montserratbold";
    font-size: 16px;
    margin: 0;
    margin-bottom: 1px;
    color: #0b4e35; }
  .b-schedule-item__address-court {
    font-family: "montserratmedium";
    margin: 0;
    margin-bottom: 5px; }
  .b-schedule-item__additional-court {
    margin: 0;
    color: #cccccc; }

@media (min-width: 768px) {
  .b-schedule-item {
    border: none;
    margin: 0;
    background-color: #fff;
    cursor: default; }
    .b-schedule-item__time {
      font-size: 22px; }
    .b-schedule-item__info {
      border-bottom: none; }
    .b-schedule-list__item--hover .b-schedule-item {
      cursor: pointer; } }

@media (min-width: 1280px) {
  .b-schedule-item__state-name--block {
    display: block; }
  .b-schedule-item__time-wrap {
    padding-top: 7px; }
  .b-schedule-item__state {
    font-size: 19px; }
    .b-schedule-item__state:before {
      top: 8px; } }



