/** ----- colors ----- */
.b-profile-tab {
  list-style-type: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px 0;
  margin-bottom: 20px; }
  .b-profile-tab.active .b-profile-tab__text {
    color: #ef396f;
    font-family: "montserratbold"; }
  .b-profile-tab__icon {
    width: 30px;
    flex-shrink: 0;
    margin-right: 15px; }
  .b-profile-tab__text {
    display: inline-block;
    margin: 0;
    transition: color 0.3s; }

@media (min-width: 1024px) {
  .b-profile-tab {
    margin-bottom: 25px;
    padding: 8px;
    transition: 0.15s;
    border: 1px solid transparent;
    border-radius: 5px; }
    .b-profile-tab.active {
      border-color: #0b4e35;
      background-color: #0b4e3536; }
    .b-profile-tab__icon {
      width: auto;
      max-width: 100%;
      margin: 0px; }
    .b-profile-tab__text {
      display: none; } }



