/** ----- colors ----- */
.b-my-booking-card {
  font-size: 16px;
  position: relative;
  border-left: 2px solid transparent;
  padding-left: 15px;
  line-height: 1.3;
  min-height: 320px; }
  .b-my-booking-card.accept {
    border-color: #0b4e35; }
  .b-my-booking-card.wait {
    border-color: orange; }
  .b-my-booking-card.cancel {
    border-color: #e92617; }
  .b-my-booking-card__title {
    font-weight: 700;
    margin: 0;
    padding: 0; }
  .b-my-booking-card__status {
    padding-right: 40px; }
    .accept .b-my-booking-card__status {
      color: #0b4e35; }
    .wait .b-my-booking-card__status {
      color: orange; }
    .cancel .b-my-booking-card__status {
      color: #e92617; }
  .b-my-booking-card > div + div {
    margin-top: 15px; }
  .b-my-booking-card__cost-text {
    font-size: 24px; }



