/** ----- colors ----- */
.b-date-calendar {
  padding-top: 10px;
  padding-right: 8px;
  padding-bottom: 20px;
  padding-left: 8px;
  background-color: #0b4e35; }
  .b-date-calendar__calendar {
    font-family: "montserratmedium";
    width: 100%;
    max-width: 320px;
    margin: 0px auto;
    border: none;
    background-color: transparent; }
  .b-date-calendar .react-calendar--selectRange .react-calendar__tile--hover {
    background: rgba(255, 255, 255, 0.2); }
  .b-date-calendar .react-calendar__month-view__weekdays__weekday {
    font-family: "montserratmedium";
    font-size: 14px;
    opacity: .48;
    color: #fff; }
    @media (min-width: 768px) {
      .b-date-calendar .react-calendar__month-view__weekdays__weekday {
        font-size: 20px; } }
  .b-date-calendar .react-calendar__month-view__days__day {
    color: #fff; }
    .b-date-calendar .react-calendar__month-view__days__day--neighboringMonth {
      opacity: 1;
      color: rgba(255, 255, 255, 0.65); }
  .b-date-calendar .react-calendar__tile {
    font-family: "montserratmedium";
    font-size: 14px;
    transition-duration: 0.3s;
    transition-property: box-shadow;
    border: 1px solid #0b4e35;
    border-radius: 3px; }
    .b-date-calendar .react-calendar__tile:enabled:hover, .b-date-calendar .react-calendar__tile:enabled:active {
      background: rgba(255, 255, 255, 0.2);
      background-color: transparent; }
    .b-date-calendar .react-calendar__tile--active {
      color: #121212;
      background: #fff; }
      .no-touchevents .b-date-calendar .react-calendar__tile--active:enabled:hover,
      .touchevents .b-date-calendar .react-calendar__tile--active:enabled:active {
        background: #fff; }
    .b-date-calendar .react-calendar__tile--now {
      font-family: "montserratbold";
      color: #ef396f;
      border: 1px solid #ef396f; }
    .b-date-calendar .react-calendar__tile:enabled:focus {
      background-color: transparent; }
    .b-date-calendar .react-calendar__tile:disabled {
      color: rgba(255, 255, 255, 0.15);
      background-color: transparent; }
  .b-date-calendar button.react-calendar__tile--active:enabled:hover,
  .b-date-calendar button.react-calendar__tile--active:enabled:focus {
    background: #fff; }
  .b-date-calendar .react-calendar__navigation__label {
    font-family: "montserratregular";
    font-size: 20px;
    padding-top: 6px;
    padding-bottom: 0px;
    color: #fff; }
  .b-date-calendar .react-calendar__navigation__arrow {
    font-size: 30px;
    transition-duration: 0.3s;
    transition-property: background-color, color;
    color: #fff; }
    .no-touchevents .b-date-calendar .react-calendar__navigation__arrow:enabled:hover,
    .touchevents .b-date-calendar .react-calendar__navigation__arrow:enabled:active {
      color: #0b4e35;
      background-color: #fff; }
    .b-date-calendar .react-calendar__navigation__arrow:enabled:focus {
      color: #fff;
      background-color: transparent; }
    .b-date-calendar .react-calendar__navigation__arrow:disabled {
      color: #a7a7a7; }
  .b-date-calendar .react-calendar__navigation button[disabled] {
    background-color: transparent; }
  .b-date-calendar .react-calendar__navigation__next2-button,
  .b-date-calendar .react-calendar__navigation__prev2-button {
    display: none; }

@media (min-width: 1280px) {
  .b-date-calendar {
    order: 1;
    flex-shrink: 0;
    width: 320px; } }



