/** ----- colors ----- */
.b-time-field {
  margin-bottom: 40px; }
  .b-time-field__label {
    font-family: "montserratbold";
    font-size: 16px;
    line-height: 1.4;
    display: inline-block;
    margin-bottom: 5px; }
  .b-time-field__input {
    font-size: 16px;
    line-height: 1.4;
    border-bottom: 2px solid #0b4e35;
    color: #2c2c2c;
    height: 40px;
    display: flex;
    align-items: center;
    cursor: pointer; }
  .b-time-field__keeper-wrapper {
    display: block; }

.outside_container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  touch-action: none;
  pointer-events: none;
  z-index: 1000;
  display: flex; }

.modal_container {
  top: 0 !important;
  width: 300px !important;
  margin: auto !important;
  position: static !important;
  touch-action: auto;
  pointer-events: all; }

.preview_container {
  padding-left: 0px !important;
  left: 0px !important;
  transform: translateX(0) !important; }

.time_picker_preview {
  box-shadow: none !important;
  border-bottom: 1px solid #0b4e35; }



