/** ----- colors ----- */
.b-modal__title {
  margin-top: 0px; }

.b-modal__title-group {
  font-family: "montserratbold";
  font-size: 18px;
  color: #0b4e35; }

.b-modal__button-wrapper {
  text-align: center; }

.ReactModal__Content {
  top: auto !important;
  left: 0px !important;
  right: 0px !important;
  bottom: auto !important;
  padding: 15px !important;
  max-width: 520px;
  box-shadow: 0 2px 6px rgba(40, 36, 35, 0.1);
  min-height: 100%; }

.ReactModal__Overlay {
  z-index: 98;
  background-color: rgba(0, 8, 14, 0.85) !important;
  overflow: auto; }

@media (min-width: 768px) {
  .ReactModal__Content {
    margin: 60px auto;
    padding: 30px !important;
    min-height: 0px; } }



