/** ----- colors ----- */
.b-cost-information {
  font-family: "montserratmedium";
  font-size: 14px;
  margin-bottom: 25px; }
  .b-cost-information__title {
    line-height: 18px;
    margin-bottom: 5px;
    color: #0b4e35; }
  .b-cost-information__cost {
    font-size: 18px;
    line-height: 22px;
    padding: 10px 7px 7px 10px;
    vertical-align: baseline;
    color: #ef396f;
    border: 1px solid #eee;
    border-radius: 3px;
    background-color: rgba(249, 250, 251, 0.8); }

@media (min-width: 1024px) {
  .b-cost-information {
    font-size: 16px;
    margin-bottom: 30px; }
    .b-cost-information__title {
      margin-bottom: 8px; } }



