/** ----- colors ----- */
.b-schedule {
  overflow: hidden;
  margin: 0px -15px;
  padding: 0px 15px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background: #ffffff; }
  .b-schedule__date {
    margin-right: -15px;
