/** ----- colors ----- */
.b-booking-request {
  font-size: 0;
  margin-right: -15px;
  margin-left: -15px; }
  .b-booking-request > p {
    font-family: "montserratbold";
    margin-bottom: 50px;
    padding: 0px 15px; }
  .b-booking-request h2 {
    margin-bottom: 30px;
    padding: 0px 15px; }
  .b-booking-request__item {
    padding: 20px 15px; }
    .b-booking-request__item + .b-booking-request__item {
      border-top: 1px solid #0b4e35; }
  .b-booking-request__player-name {
    font-size: 18px;
    line-height: 26px; }
  .b-booking-request__player-tel {
    font-size: 16px;
    line-height: 1; }
  .b-booking-request__training {
    margin-top: 15px;
    margin-bottom: 20px; }
  .b-booking-request__training-playground {
    font-size: 16px;
    line-height: 21px;
    color: #0b4e35; }
  .b-booking-request__training-time {
    font-size: 14px;
    line-height: 21px;
    color: #ef396f; }
  .b-booking-request__buttons {
    display: flex;
    flex-flow: row nowrap; }
    .b-booking-request__buttons > * + * {
      margin-left: 15px; }

@media (min-width: 768px) {
  .b-booking-request__item {
    display: inline-block;
    vertical-align: top;
    border: 1px solid #ebebeb;
    border-radius: 5px;
    padding: 20px;
    background-color: #fff;
    width: calc(50% - 30px);
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 30px; }
    .b-booking-request__item + .b-booking-request__item {
      border: none; } }

@media (min-width: 1280px) {
  .b-booking-request__item {
    width: calc(100% / 3 - 30px); }
  .b-booking-request__player-name {
    font-size: 20px;
    line-height: 32px; }
  .b-booking-request__player-tel {
    font-size: 18px;
    line-height: 30px; }
  .b-booking-request__training-playground {
    font-size: 17px;
    line-height: 25px; }
  .b-booking-request__training-time {
    font-size: 16px;
    line-height: 25px; } }



