/** ----- colors ----- */
.b-registration {
  position: absolute;
  top: 0px;
  left: 0px;
  overflow: auto;
  width: 100%;
  min-height: 100vh;
  background-image: url(../../../style/images/back.jpg);
  background-size: cover; }
  .b-registration .container {
    display: flex;
    min-height: 100%;
    padding-top: 100px;
    padding-bottom: 64px; }
  .b-registration__form-wrapper {
    width: 100%;
    max-width: 420px;
    margin: auto; }
  .b-registration__form {
    width: 100%;
    padding: 15px;
    border-radius: 5px;
    background: rgba(18, 18, 18, 0.95); }
  .b-registration__heading {
    text-align: center;
    color: #fff; }
  .b-registration__button-wrapper {
    position: relative;
    text-align: center; }
  .b-registration__error {
    font-family: "montserratbold";
    font-size: 14px;
    line-height: 1.3;
    position: absolute;
    top: 100%;
    left: 0px;
    width: 100%;
    padding-top: 15px;
    color: #ff0b0b; }
  .b-registration__sub-navigation {
    font-size: .88em;
    line-height: 2;
    display: flex;
    width: 100%;
    margin-top: 15px;
    padding: 10px 15px;
    color: #cccccc;
    border-radius: 5px;
    background: rgba(18, 18, 18, 0.8);
    align-items: center;
    flex-flow: row wrap;
    justify-content: space-between; }
  .b-registration__sub-question {
    margin-right: 10px; }

@media (min-width: 768px) {
  .b-registration__form-wrapper {
    max-width: 460px; }
  .b-registration__form {
    padding: 30px; }
  .b-registration__sub-navigation {
    padding: 15px 30px; }
  .b-registration__error {
    font-size: 16px;
    left: 0px;
    width: 100%; } }



