/** ----- colors ----- */
.b-button {
  display: inline-block;
  padding: 10px 15px;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  background: #0b4e35; }
  .no-touchevents .b-button:hover,
  .touchevents .b-button:active {
    background-color: #0d6041; }
  .b-button--full {
    width: 100%; }
  .b-button--hollow {
    color: #121212;
    border: 1px solid #0b4e35;
    background: none; }
    .no-touchevents .b-button--hollow:hover,
    .touchevents .b-button--hollow:active {
      color: #0b4e35;
      background-color: #0d60412c; }
  .b-button--save {
    width: 100%;
    border-radius: 0; }
  .b-button--hello-page {
    width: 290px; }
  .b-button--login {
    width: 100%;
    color: #ffffff;
    border: 1px solid #0b4e35;
    background: none; }
    .no-touchevents .b-button--login:hover,
    .touchevents .b-button--login:active {
      color: #ffffff;
      background-color: #0d60412c; }

@media (min-width: 1024px) {
  .b-button {
    padding: 12px 24px; } }

@media (min-width: 1280px) {
  .b-button {
    font-size: 16px; } }



