/** ----- colors ----- */
.b-checkbox {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 50px; }
  .b-checkbox--hide {
    display: none; }
  .b-checkbox--add-schedule {
    margin-bottom: 20px; }
    .b-checkbox--add-schedule:not(:last-child) {
      margin-bottom: 40px; }
  .b-checkbox__input {
    display: none; }
  .b-checkbox__label {
    font-size: 14px;
    display: inline-block;
    padding-top: 3px;
    cursor: pointer;
    transition: color 0.3s;
    color: #757575; }
    .no-touchevents .b-checkbox__label:hover,
    .touchevents .b-checkbox__label:active {
      color: #fff; }
  .b-checkbox__check {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 30px;
    margin: 0;
    margin-right: 10px;
    cursor: pointer;
    transform: translate3d(0, 0, 0);
    -webkit-tap-highlight-color: transparent; }
    .no-touchevents .b-checkbox__check:hover + .b-checkbox__label,
    .touchevents .b-checkbox__check:active + .b-checkbox__label {
      color: #fff; }
    .b-checkbox__check:before {
      position: absolute;
      top: -15px;
      left: -15px;
      width: 48px;
      height: 48px;
      content: '';
      transition: opacity .2s ease;
      opacity: 0;
      border-radius: 50%;
      background: rgba(34, 50, 84, 0.03); }
    .b-checkbox__check svg {
      position: relative;
      z-index: 1;
      transition: all .2s ease;
      transform: translate3d(0, 0, 0);
      fill: none;
      stroke: #0b4e35;
      stroke-linecap: round;
      stroke-linejoin: round;
      stroke-width: 1.5; }
      .b-checkbox__check svg path {
        stroke-dasharray: 60;
        stroke-dashoffset: 0; }
      .b-checkbox__check svg polyline {
        stroke-dasharray: 22;
        stroke-dashoffset: 66; }
    .b-checkbox__check:hover:before {
      opacity: 1; }
    .b-checkbox__check:hover svg {
      stroke: #0b4e35; }
    .b-checkbox__check--check svg {
      stroke: #0b4e35; }
      .b-checkbox__check--check svg path {
        transition: all .3s linear;
        stroke-dashoffset: 60; }
      .b-checkbox__check--check svg polyline {
        transition: all .2s linear;
        transition-delay: .15s;
        stroke-dashoffset: 42; }
  .no-touchevents .b-checkbox--add-schedule .b-checkbox__check:hover + .b-checkbox__label,
  .touchevents .b-checkbox--add-schedule .b-checkbox__check:active + .b-checkbox__label {
    color: #ef396f; }
  .no-touchevents .b-checkbox--add-schedule .b-checkbox__label:hover,
  .touchevents .b-checkbox--add-schedule .b-checkbox__label:active {
    color: #ef396f; }

@media (min-width: 1280px) {
  .b-checkbox {
    margin-bottom: 60px; }
    .b-checkbox--add-schedule {
      margin-bottom: 20px; }
      .b-checkbox--add-schedule:not(:last-child) {
        margin-bottom: 40px; }
    .b-checkbox__label {
      font-size: 16px; } }



