/** ----- colors ----- */
.b-booking-form {
  width: 100%;
  max-width: 600px;
  margin: 0px auto; }
  .b-booking-form__fieldset {
    margin-bottom: 40px;
    padding: 15px 15px 0px; }
  .b-booking-form__note {
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 18px;
    color: #8c8c8c; }
  .b-booking-form__button:not(:last-child) {
    margin-bottom: 25px; }

@media (min-width: 768px) {
  .b-booking-form__button {
    width: 50%;
    display: inline-block; }
    .b-booking-form__button:first-child {
      padding-right: 10px; }
    .b-booking-form__button:last-child {
      padding-left: 10px; }
    .b-booking-form__button:not(:last-child) {
      margin-bottom: 0px; } }



