/** ----- colors ----- */
.b-input {
  margin-bottom: 30px; }
  .b-input--inline {
    display: inline-block; }
  .b-input--time-booking {
    display: inline-block;
    width: calc(50% - 10px); }
    .b-input--time-booking:nth-child(2) {
      margin-right: 20px; }
  .b-input__input {
    font-size: 16px;
    line-height: 1.4;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #0b4e35;
    width: 100%;
    color: #fff;
    height: 40px; }
    .b-input__input:focus {
      outline: none; }
  .b-input__label {
    font-family: "montserratbold";
    font-size: 16px;
    line-height: 1.4;
    display: inline-block;
    margin-bottom: 5px; }
  .b-input--textarea .b-input__input {
    height: 80px;
    resize: none; }
  .b-input--textarea .b-input__label {
    margin-bottom: 15px; }
  .b-input--black-color .b-input__input {
    color: #2c2c2c; }

@media (min-width: 1280px) {
  .b-input__input {
    font-size: 16px; } }



